/*            Navigation menu                */
.nav-pills {

  > li + li {
    margin-left: 0;
  }
  > li > a {
    border: 1px solid $info-color;
    border-radius: 0;
    color: $info-color;
    margin-left: -1px;

    &:hover,
    &:focus{
      background-color: #F5F5F5;
    }
  }
  > li.active > a,
  > li.active > a:hover,
  > li.active > a:focus {
    background-color: $info-color;
    color: #FFFFFF;
  }
  > li:first-child > a{
    border-radius: 4px 0 0 4px;
    margin: 0;
  }
  > li:last-child > a{
    border-radius: 0 4px 4px 0;
  }

}

.pagination{
  .page-item{
    .page-link{
      border-radius: 50%;
      margin: 0 2px;
      color: #797979;
      padding: 0;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 35px;
      height: 35px;
      user-select: none;

      &:hover{
        background-color: #eee;
        border-color: #ddd;
      }
    }

    &.active{
      .page-link{
        background-color: #23CCEF;
        border: 0;
        color: #FFFFFF !important;
        padding: 7px 13px;
      }
    }
  }


  .page-item{
    &:first-child,
    &:last-child{
      .page-link{
        border-radius: 50%;
      }
    }
  }
}

.pagination > li.disabled{
  opacity: .4;
}

.pagination.pagination-no-border > li > a,
.pagination.pagination-no-border > li > span{
  border: 0;
}
.pagination > li > a,
.pagination > li > span,
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span{
  border-radius: 50%;
  margin: 0 2px;
  color: $default-states-color;
}
.pagination > li.active > a,
.pagination > li.active > span,
.pagination > li.active > a:hover,
.pagination > li.active > span:hover,
.pagination > li.active > a:focus,
.pagination > li.active > span:focus {
  background-color: $info-color;
  border: 0;
  color: #FFFFFF;
  padding: 7px 13px;
}

.nav-pills-blue{
  > li.active {
    > a,
    > a:hover,
    > a:focus{
      background-color: $color-blue;
    }
  }
}

.pagination-blue{
  > li.active{
    a,
    a:hover,
    a:focus,
    span,
    span:hover,
    span:focus{
      background-color: $color-blue !important;
    }
  }
}

